import React, { Component } from "react";
import { Col, Row, Container } from "reactstrap";
import Header from "../header";
import RandomChar from "../randomChar";
import ErrorMessage from "../errorMessage";
import { CharacterPage, BookPage, HousePage, BooksItem } from "../pages";
import { BrowserRouter as Router, Route, Switch, Link } from "react-router-dom";

import GotService from "../../services/gotService";

export default class App extends Component {
  gotService = new GotService();
  constructor() {
    super();
    this.changeShow = this.changeShow.bind(this);
  }

  state = {
    showRandomChar: true,
    selectedItem: null,
    error: false,
  };

  componentDidCatch() {
    console.log("error");
    this.setState({
      error: true,
    });
  }
  changeShow() {
    this.setState((state) => {
      return {
        showRandomChar: !state.showRandomChar,
      };
    });
  }

  render() {
    if (this.state.error) {
      return <ErrorMessage />;
    }
    return (
      <Router>
        <div className="app">
          <Container>
            <Header />
          </Container>
          <Container>
            <Row>
              <Col lg={{ size: 5, offset: 0 }}>
                {this.state.showRandomChar ? <RandomChar /> : ""}
                <button
                  className="btn btn-danger mb-5"
                  onClick={this.changeShow}
                >
                  Показать скрыть
                </button>
              </Col>
            </Row>

            <Switch>
              <Route path="/" exact component={() => <h1 className="bg-light px-5 py-5">Welcome GOT </h1>} />
              <Route path="/characters" component={CharacterPage} />
              <Route path="/houses" component={HousePage} />
              <Route path="/books" exact component={BookPage} />
              <Route
                path="/books/:id"
                render={({ match, location, history }) => {
                  const { id } = match.params;

                  return <BooksItem bookId={id} />;
                }}
              />
              <Route
                path="*"
                render={(obj) => {
                  return <My404Component link={obj} />;
                }}
              />
            </Switch>
          </Container>
        </div>
      </Router>
    );
  }
}

const My404Component = (props) => {
  console.log(props);
  return (
    <>
     <Link to='/' className='btn btn-danger' >На главную</Link>
      <img
        src="data:image/png;base64,"
        alt="ss"
      />
    </>
  );
};
